.Avatarborder{
  border: 2px solid white;
  padding: 3px;
}
nav ul li {
    list-style: none;
}
nav ul li a {
    text-decoration: none;
    color: black;
    font-family: 'Mona Sans';
    font-weight: 600;
    font-size: 14px;
}
.logo h1{
    margin: 0;
    color:black;
}
.ccc ul {
    padding: 0;
}
.resposive-navbar{
    display: none;
}
.nav-link a{
text-decoration: none;
color: black;
}
.nav-link{
    padding-left: 12px;
}
ul.d-flex.gap-3.justify-content-center.white-clr li a {
    color: white;
}
.nav-mobile{
flex-direction: column;

}
.nav-mobile h4{
color: black;
margin: 0;
}
.resposive-nav{
    display:none;
}
